.trees__wrapper {
  position: relative;
  padding: 0.5rem 0;
  overflow-x: auto;
  overflow-y: auto;
}
.trees__wrapper::-webkit-scrollbar {
  height: 7px;
  width: 7px;
  border-radius: 10px;
  background: transparent;
}
.trees__wrapper::-webkit-scrollbar-thumb {
  height: 20px;
  border-radius: 10px;
  border-width: 1.5px;
  border-style: solid;
  border-color: transparent;
  background-color: #d6d6d6;
  background-clip: padding-box;
}
.trees__wrapper::-webkit-scrollbar-thumb:hover {
  background-color: #949494;
  background-clip: border-box;
}
.trees__wrapper .tree__diagram {
  display: flex;
  flex-direction: column;
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
  --trees-padding-left: 1rem;
  --trees-padding-y: 0.25rem;
}
.trees__wrapper .tree__diagram .tree__diagram__item {
  position: relative;
  padding-left: var(--trees-padding-left);
}
.trees__wrapper .tree__diagram .tree__diagram__item .tree__item {
  display: flex;
  align-items: center;
}
.trees__wrapper .tree__diagram .tree__diagram__item .tree__item .arrow {
  min-width: 1em;
  cursor: pointer;
  z-index: 5;
  display: inline-flex;
  align-items: center;
}
.trees__wrapper .tree__diagram .tree__diagram__item .tree__item .arrow .default-icon {
  transition: transform 0.2s ease;
}
.trees__wrapper .tree__diagram .tree__diagram__item .tree__item .arrow .default-icon.rotated {
  transform: rotate(90deg);
}
.trees__wrapper .tree__diagram .tree__diagram__item .tree__item .arrow .no-animation-icon {
  display: inline-flex;
  align-items: center;
}
.trees__wrapper .tree__diagram .tree__diagram__item .tree__item .arrow .leaf-icon {
  display: inline-flex;
  align-items: center;
}
.trees__wrapper .tree__diagram .tree__diagram__item .tree__item .arrow .loading-icon {
  animation: rotate 1s linear infinite;
}
@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}
.trees__wrapper .tree__diagram .tree__diagram__item .tree__item .link-text {
  flex: 1;
  position: relative;
  display: block;
  text-decoration: none;
  z-index: 1;
  padding: var(--trees-padding-y) 0.5rem;
}
.trees__wrapper .tree__diagram .tree__diagram__item .tree__item .link-text:hover {
  cursor: pointer;
}
.trees__wrapper .tree__diagram .tree__diagram__item .tree__item .link-text:hover .hoverable {
  background: #efefef;
}
.trees__wrapper .tree__diagram .tree__diagram__item .tree__item .link-text:hover .custom-content {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.3s ease-in, visibility 0.3s ease-in;
  transition-delay: 0.2s;
}
.trees__wrapper .tree__diagram .tree__diagram__item .tree__item .link-text .hoverable {
  border-radius: 5px;
  padding: var(--trees-padding-y);
}
.trees__wrapper .tree__diagram .tree__diagram__item .tree__item .link-text .custom-content {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  right: 1rem;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
}
.trees__wrapper .tree__diagram .tree__diagram__item .tree__item .link-text .custom-content .btn {
  white-space: nowrap;
}
.trees__wrapper .tree__diagram .tree__diagram__item .tree__item .link-text .hoverable.is-active {
  color: white;
  background-color: #3162b2;
}
